}
:root {
  color-scheme: dark;
}
html, body {
  height: 100%;
}
body {
  background: #000;
}
.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}
.bg-gradient-radial {
